import { SignInService } from './sign-in.service';
import { Http, Headers } from '@angular/http';
import { Component } from '@angular/core';
import 'rxjs/add/operator/toPromise';
@Component({
    selector: 'app-sign-in',
    template:`
    <form (ngSubmit)="onSubmit(formSignIn);" #formSignIn="ngForm">
    <input 
    placeholder="Email" 
    ngModel
    name="email" 
    #txtEmail="ngModel"
    required
    email
    >
    <p *ngIf="txtEmail.touched && txtEmail.errors?.required">Email is required</p>
    <p *ngIf="txtEmail.touched && txtEmail.errors?.email">Email is invalid</p>
    <br><br>
    <input 
        type="password" 
        placeholder="Password" 
        ngModel 
        name="password" 
        #txtPassword="ngModel"
        minlength="6"
        pattern="[a-z]*"
    >  
    <br><br>
    <div ngModelGroup="subjects">
    <label><input type="checkbox"  [ngModel]="false" name="Angular">Football</label>
    <label><input type="checkbox"  [ngModel]="false" name="Nodejs">Football</label>
    <label><input type="checkbox"  [ngModel]="false" name="React">Football</label>
    </div>
    <br>
    <button >Submit</button>
    </form>
    <button (click)='postToExpress(formSignIn);'>POST</button>
    <p>{{txtPassword.errors | json}}
    <p>{{txtEmail.errors | json }}</p>
    <p>{{formSignIn.value | json }}</p>
    `,
    providers:[SignInService]
})
export class SignInComponent{
    constructor(private signInService: SignInService){}
    onSubmit(formSignIn){
        this.signInService.onSubmit(formSignIn);
    }
    postToExpress(formSignIn){
        this.signInService.postToExpress(formSignIn)
        .then(res=>console.log(res))
        .catch(err=>console.log(err));
    }
}