
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
@Component({
    selector: 'app-sign-up',
    template: `
    <form (ngSubmit)="onSubmit();" [formGroup]="formSignUp">
        <input placeholder="Email" formControlName='email'>
        <p *ngIf="formSignUp.get('email').invalid && formSignUp.get('email').touched"></p>
        <br><br>
        <input type="password" placeholder="Password" formControlName="password">
        <br><br>
        <div formGroupName="subject">
            <label><input formControlName='nodeJS' type="checkbox" name="NodeJs">NodeJs  </label>
            <label><input formControlName='angular' type="checkbox" name="NodeJs">Angular </label>
            <label><input formControlName='reactJS' type="checkbox" name="NodeJs">Reactjs </label>
        </div>
        <br><br>
        <button [disabled]='formSignUp.invalid'>Submit</button>
    </form>
    `
})
export class SignUpComponent implements OnInit {
    formSignUp: FormGroup;//cần gán vào formGroup
    constructor(private fb: FormBuilder) {}
    ngOnInit(): void {
        this.formSignUp = this.fb.group({
            email: ['', Validators.required],
            password: '',
            subject: this.fb.group({
                nodeJS: false,
                angular: true,
                reactJS: false
            })
        })
    }
    onSubmit() {
        console.log(this.formSignUp.value);
    }
}