import { ChildComponent } from './child.component';
import {Component, ViewChild} from "@angular/core";
@Component({
    selector:'app-parent',
    template:`  
    <button (click)='onAddForChild()'>Add for Child</button>
    <app-child #child></app-child>`
})
export class ParentComponent{
    //khai báo một viewchild commponent
    @ViewChild(ChildComponent)

    //khởi tạo một biến myChild đại diện cho ChildComponent
    myChild: ChildComponent;
    value=0;
    changeValue(isAdd: boolean){
        if(isAdd)
        this.value++;
        else this.value--;
    }
    onAddForChild(){
        this.myChild.value++;
    }
}
//#child ở đây đại diện cho app-child
//app-child chứa một trường là value
//child có thể truy cập và trường value này và thay đổi giá trị