import { Injectable } from "@angular/core";
import { Http } from '@angular/http';

@Injectable()
export class IpService{
    ip: string;
    projectName: String;
    items =[];
    constructor(private http: Http){}
    getIp(){
        return this.http.get('https://localhost:44342/projects')
        .toPromise()
        .then(res=>res.json())
        .then(resJson=>this.items=resJson)
        .catch(err=>console.log(err));
    }
    
}